            <v-icon class="mr-1">mdi-cash-multiple</v-icon>AUTONOMIA PARA LIMITES DE CRÉDITO
          </v-card-title>
          <v-card-text
            class="body-2"
          >
            <v-layout
              row
              wrap>
              <v-flex md12>
                Informe abaixo os limites de crédito máximos permitidos para os clientes com restrição cadastral (SERASA / SPC / NEUROTECH / CRIVO) ou abaixo da pontuação mínima de SCORE.
              </v-flex>
              <v-flex
                md2
                sm6
                xs12>
                <v-text-field
                  v-formata-moeda="item.permissao.limiteCredMensalRestricaoSPC"
                  v-money="money"
                  v-model.lazy="item.permissao.limiteCredMensalRestricaoSPC"
