                  suffix="R$"
                  reverse
                  label="Limite Crédito Mensal"
                />
              </v-flex>
              <v-flex
                md2
                sm6
                xs12
                class="ml-3"
              >
                <v-text-field
                  v-formata-moeda="item.permissao.limiteCredGlobalRestricaoSPC"
                  v-money="money"
                  v-model.lazy="item.permissao.limiteCredGlobalRestricaoSPC"
                  type="tel"
                  suffix="R$"
                  reverse
                  label="Limite Crédito Global"
                />
              </v-flex>
              <v-flex md12>
                Informe abaixo os limites de crédito máximos permitidos para os clientes com restrição cadastral (cruzamento de dados).
              </v-flex>
              <v-flex
                md2
                sm6
                xs12>
                <v-text-field
                  v-formata-moeda="item.permissao.limiteCredMensalRestricaoCruzarDados"
                  v-money="money"
                  v-model.lazy="item.permissao.limiteCredMensalRestricaoCruzarDados"
                  type="tel"
                  suffix="R$"
                  reverse
                  label="Limite Crédito Mensal"
                />
              </v-flex>
              <v-flex
                md2
                sm6
                xs12
                class="ml-3"
              >
                <v-text-field
                  v-formata-moeda="item.permissao.limiteCredGlobalRestricaoCruzarDados"
                  v-money="money"
                  v-model.lazy="item.permissao.limiteCredGlobalRestricaoCruzarDados"
                  type="tel"
                  suffix="R$"
                  reverse
                  label="Limite Crédito Global"
                />
              </v-flex>
              <v-flex md12>
                Informe abaixo os limites de crédito máximos permitidos para os clientes com restrição cadastral (grupo de risco).
              </v-flex>
              <v-flex
                md2
                sm6
                xs12>
                <v-text-field
                  v-formata-moeda="item.permissao.limiteCredMensalRestricaoGrupoRisco"
