              </v-flex>
              <v-flex
                md2
                sm6
                xs12>
                <v-text-field
                  v-formata-moeda="item.permissao.limiteCredMensal"
                  v-money="money"
                  v-model.lazy="item.permissao.limiteCredMensal"
                  type="tel"
                  suffix="R$"
                  reverse
                  label="Limite Crédito Mensal"
