                  v-model.lazy="item.permissao.limiteCredGlobal"
                  type="tel"
                  suffix="R$"
                  reverse
                  label="Limite Crédito Global"
                />
              </v-flex>
            </v-layout>
          </v-card-text>
          <v-card-title class="title grey lighten-3">
