                xs12
                class="ml-3"
              >
                <v-text-field
                  v-model="item.permissao.qtdDiasMaxCobranca"
                  min="0"
                  type="number"
                  prefix="dias"
                  reverse
                  label="Máximo Dias Cobrança"
                />
              </v-flex>
              <v-flex
                md7
                sm12
                xs12
                class="ml-3"
              >
                <v-checkbox
                  v-model="item.permissao.gerarFilaCobranca"
                  label="Gerar fila de cobrança"
                />
              </v-flex>
            </v-layout>
