                  label="Visualizar resposta em análise visual na inclusão do cliente"
                />
              </v-flex>
              <v-flex
                md6
                sm12
                xs12
              >
                <v-checkbox
                  v-model="item.permissao.permitirCadastroAnaliseVisual"
                  class="checkbox-custom"
                  label="Permitir cadastro com consulta em análise visual"
                />
              </v-flex>
