                sm12
                xs12
                class="mb-3"
              >
                Selecione os campos permitidos para pesquisar o cliente
              </v-flex>
              <v-flex
                md2
                sm3
                xs6
              >
                <v-checkbox
                  v-model="item.permissao.pesquisaCPF"
                  class="checkbox-custom"
                  label="CPF"
                />
              </v-flex>
              <v-flex
                md2
                sm3
                xs6
              >
                <v-checkbox
                  v-model="item.permissao.pesquisaNome"
                  class="checkbox-custom"
                  label="Nome"
                />
              </v-flex>
              <v-flex
                md2
                sm3
                xs6
              >
                <v-checkbox
                  v-model="item.permissao.pesquisaIdentidade"
                  class="checkbox-custom"
                  label="Identidade"
                />
              </v-flex>
              <v-flex
                md2
                sm3
                xs6
              >
                <v-checkbox
                  v-model="item.permissao.pesquisaDataCadastro"
                  class="checkbox-custom"
                  label="Data Cadastro"
                />
              </v-flex>
              <v-flex
                md2
                sm3
                xs6
              >
                <v-checkbox
                  v-model="item.permissao.pesquisaTelefone"
                  class="checkbox-custom"
                  label="Telefone"
                />
              </v-flex>
            </v-layout>
