                sm12
                xs12
                class="mb-3"
              >
                Selecione as informações do cliente que o perfil terá o direito de visualizar nos relatórios / telas.
              </v-flex>
              <v-flex
                md2
                sm3
                xs6
              >
                <v-checkbox
                  v-model="item.permissao.visualizarTelefone"
                  class="checkbox-custom"
                  label="Telefone"
                />
              </v-flex>
              <v-flex
                md2
                sm3
                xs6
              >
                <v-checkbox
                  v-model="item.permissao.visualizarEndereco"
                  class="checkbox-custom"
                  label="Endereço"
                />
              </v-flex>
              <v-flex
                md2
                sm3
                xs6
              >
                <v-checkbox
                  v-model="item.permissao.visualizarCPF"
                  class="checkbox-custom"
                  label="CPF"
                />
              </v-flex>
              <v-flex
                md2
                sm3
                xs6
              >
                <v-checkbox
                  v-model="item.permissao.visualizarNascimento"
                  class="checkbox-custom"
                  label="Nascimento"
                />
              </v-flex>
              <v-flex
                md2
                sm3
                xs6
              >
                <v-checkbox
                  v-model="item.permissao.visualizarScore"
                  class="checkbox-custom"
