                />
              </v-flex>
              <v-flex
                md2
                sm3
                xs6
              >
                <v-checkbox
                  v-model="item.permissao.visualizarLimites"
                  class="checkbox-custom"
                  label="Limites/Saldos"
                />
              </v-flex>
              <v-flex
                md12
                sm12
                xs12
                class="mt-3 mb-3"
              >
                Outras permissões
              </v-flex>
              <v-flex
                md12
                sm12
                xs12
              >
                <v-checkbox
                  v-model="item.permissao.limitarAutonomia"
                  class="checkbox-custom"
                  label="Limitar autonomia de limite de crédito, pagamento fatura WEB e autorizações de compra a própria entidade (rede ou loja)"
                />
              </v-flex>
              <v-flex
                md6
                sm12
                xs12
              >
                <v-checkbox
                  v-model="item.permissao.alterarApenasClientesDaEntidade"
                  class="checkbox-custom"
                  label="Alterar apenas os clientes da entidade de origem"
                />
              </v-flex>
              <v-flex
                md6
                sm12
                xs12
              >
                <v-checkbox
                  v-model="item.permissao.visualizarPagEmBanco"
                  class="checkbox-custom"
                  label="Visualizar os pagamentos em banco de sua rede"
                />
              </v-flex>
              <v-flex
                md6
                sm12
                xs12
              >
                <v-checkbox
                  v-model="item.permissao.visualizarBoletoFatura"
                  class="checkbox-custom"
                  label="Visualizar boleto na fatura"
                />
              </v-flex>
              <v-flex
                md6
                sm12
                xs12
              >
                <v-checkbox
                  v-model="item.permissao.visualizarMsgStatusProcessamento"
                  class="checkbox-custom"
                  label="Visualizar mensagem de status do processamento"
                />
              </v-flex>
              <v-flex
                md6
                sm12
                xs12
              >
                <v-checkbox
                  v-model="item.permissao.visualizarParcelamentoFatura"
                  class="checkbox-custom"
                  label="Visualizar parcelamento na fatura"
                />
              </v-flex>
