                md12
                sm12
                xs12
                class="layout align-center"
              >
                Dias de validade da última alteração de telefones/e-mail:
                <v-text-field
                  v-model="item.permissao.diasValidadeUltimaAlteracaoTelefoneEmail"
                  min="0"
                  type="number"
                  prefix="dias"
                  class="mr-2"
                  reverse
