<script>
import { RoleBusiness } from '../../business'
import { MONEY } from '../../constants'

export default {
  metaInfo: {
    titleTemplate: '%s - Perfis de Acesso'
  },
  data () {
    return {
      roles: [],
      loading: false,
      money: MONEY
    }
  },
  beforeMount () {
    this.loading = true
    RoleBusiness.findAll()
      .then(response => {
        this.roles = response.data
        this.loading = false
      }).catch(error => {
        this.loading = false
        this.$root.showErro(error.data)
      })
  },
  methods: {
    salvarPerfil (item) {
      this.loading = true
      RoleBusiness.salvar(item)
        .then(response => {
          this.$root.showSucesso(response.data)
          this.loading = false
        }).catch(error => {
          this.$root.showErro(error.data)
          this.loading = false
        })
    }
  }
}
</script>
<style scoped>
  .checkbox-custom {
    padding: 0px;
    margin: 0px;
    height: 30px;
  }
</style>
